@import "../base/settings";

// delete component styles

.delete_con {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -70%);
	background-color: white;
	height: 200px;
	width: 400px;
	border: solid 1px gray;
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 20px 10px 30px 10px;
	box-sizing: border-box;
	z-index: 55;
	box-shadow: 1px 1px 10px rgb(68, 68, 68);
}
.delete_btn_con {
	display: flex;
	justify-content: space-evenly;
}

.tbtn {
	cursor: pointer;
	span {
		color: blue;
		text-decoration: underline;
	}
}

.tbtn:hover {
	span {
		color: darkblue;
		text-decoration: underline;
	}
}

#empty {
	color: #707070;
	width: 200px;
	margin-left: auto;
	margin-right: auto;

	margin-top: 70px;
}
.pro_con {
	height: auto;
	min-height: 300px;
	width: 80%;
	margin: 10px 20px;
	border-radius: 10px;
	//	background-color:white;
	//padding:20px 5px;
	//  box-shadow:0px 5px 10px gray;
}

#accounting_pro_table {
	width: 100%;
}

.det_cont {
