.vouch_to {
	color: $dark;
	margin-left: 20px;
	margin-right: 10px;
	font-size: 1.2rem;
	font-weight: 600;
}
.det_cont_vouch {
	width: 100%;
	height: 5rem;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	border: 1px solid rgb(172, 172, 172);
	border-radius: 10px;
	margin-bottom: 1rem;
	background-color: white;
	box-shadow: 0px 0px 10px #f0f0f0;
}
.acc_name_vouch {
	color: $pri_color;
	font-size: 1.3rem;
	font-weight: 500;
}
.vouch_costumer_name {
	font-size: 1.3rem;
	color: $dark;
}
.acc_id_vouch {
	margin-right: 5px;
	color: $dark;
	font-size: 1.1rem;
}
.vouch_bill_detail {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.det_cont_right_vouch {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	width: 30rem;
}
.acc_right_vouch {
	font-size: 1.2rem;
	font-weight: 600;
	color: $dark;
}
.vouch_status {
	font-size: 1.2rem;
	color: red;
	font-weight: 600;
}
.pro_con_vouch {
	height: auto;
	min-height: 300px;
	width: 80%;
	margin: 10px 20px;
	min-width: 55rem;
}
.vouch_bills {
	font-size: 1.2rem;
	margin-bottom: 1.2rem;
}
.jovouch_det {
	margin-left: 15px;
}
.det_cont_right_jovouch {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	padding: 3.1rem;
	width: 28rem;
}
.det_cont_last_jovouch {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	// margin-right: 12rem;
	width: 15rem;
}
.vouc_det_left {
	width: 45rem;
}
.vouch_right {
	width: 20rem;
	// border: solid black;
	margin-left: 8rem;
}
.vouch_amount {
	margin-left: 1rem;
}
.pad {
	padding: 10px;
	height: 100px;
}
